import React, {Component} from 'react';
import {connect} from 'react-redux';
import {CardContent} from './CardContent';
import {SetCardHeight} from '../actions/SetCardHeight';
import './css/Card.css';


 class Card extends Component {


    constructor(props){
        super(props);
        this.divRef = React.createRef();
    }

    render() {
        return (
            <div className="Card" id="card" ref={this.divRef}>
                {this.renderHeader()}
                {CardContent()}
            </div>
        );
    }

    updateHeight = () =>{
        const top = 0; //this.div.current.offsetTop;
        console.log(this.div.current.offsetTop);
        const height = document.getElementById("card").clientHeight;
        this.props.SetCardHeight(top+height);
    }

    componentDidMount() {
        this.updateHeight();
        window.addEventListener("resize", this.updateHeight)
    }

    componentDidUpdate() {
        this.updateHeight()
    }

    componentWillUnmount(){
        this.updateHeight();
    }

    renderHeader() {
        return (
                <div className="Card-Header">
                    <div className="Card-Header Container">
                        <div className="Card-Header-Image"> <img width='inherit' alt="profile image"
                        src="https://i.imgur.com/wMG2gAW.jpg"/> </div>
                        <div className="Card-Header-Description"> 
                            <div className="Card-Header-Content "> 
                                <h1>Daniel Nahle</h1>
                                <h2>Software Engineer</h2>
                                <div style={{marginTop:'1em', marginLeft:'.1em'}}>
                                    <div className="Left-Icon-Text">
                                        <img width='20px' height='20px'  alt="mail" src="https://i.imgur.com/xeKQNul.png" />
                                        <a href="mailto:danielnahle6@live.com">
                                           <h4>danielnahle6@live.com</h4>
                                        </a>
                                    </div>
                                    <div className="Left-Icon-Text">
                                        <img width='20px' height='20px' alt="phone" src="https://i.imgur.com/x0LkhNl.png" />
                                        <a href="sms:3108506817">
                                           <h4>+1 (310) 850 6817</h4>
                                        </a>
                                    </div>
                                    <div className="Left-Icon-Text">
                                        <img width='20px' height='20px' alt="phone" src="https://i.imgur.com/16w84Ew.png" />
                                        <a target="_blank" href="https://www.google.com/maps/place/Redondo+Beach,+CA/">
                                           <h4>Redondo Beach, CA</h4>
                                        </a>
                                    </div>                                    
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        );
    }

}


export default connect(null, {SetCardHeight})(Card);